import { CommonModule } from '@angular/common';
import { Component, OnInit } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { environment } from '../../../../environments/environment';

@Component({
  selector: 'app-feedbacks',
  standalone: true,
  imports: [CommonModule, FormsModule],
  templateUrl: './feedbacks.component.html',
  styleUrl: './feedbacks.component.scss'
})
export class FeedbacksComponent implements OnInit {
users: any[] = [];
  searchText: string = '';
  pageSize: number = 5;
  currentPage: number = 1;

  pagination = {
    total: 0,
    page: 1,
    limit: this.pageSize
  };

  ngOnInit() {
    this.loadUsers(this.currentPage, this.pageSize);
  }

  async loadUsers(page: number = 1, limit: number = this.pageSize) {
    try {
      const token = localStorage.getItem('authToken');
      if (!token) throw new Error('Auth token not found in localStorage');

      const response = await fetch(
        `${environment.apiUrl}/contact-us?page=${page}&limit=${limit}`,
        {
          method: 'GET',
          headers: {
            Authorization: `Bearer ${token}`,
            'Content-Type': 'application/json'
          }
        }
      );

      if (!response.ok) throw new Error('Failed to fetch data');

      const result = await response.json();

      this.users = result.data?.data || [];
      this.pagination = result.data?.pagination || { total: 0, page, limit };

      // keep frontend in sync
      this.currentPage = page;
      this.pageSize = limit;

      console.log('Fetched Users:', this.users);
    } catch (error) {
      console.error('Error fetching users:', error);
    }
  }

  // total pages (from API response)
  get totalPages(): number {
    return Math.ceil(this.pagination.total / this.pagination.limit);
  }

  changePage(page: number) {
    if (page >= 1 && page <= this.totalPages) {
      this.loadUsers(page, this.pageSize);
    }
  }

  goPrevious() {
    if (this.currentPage > 1) {
      this.changePage(this.currentPage - 1);
    }
  }

  goNext() {
    if (this.currentPage < this.totalPages) {
      this.changePage(this.currentPage + 1);
    }
  }

  onPageSizeChange(event: any) {
    this.pageSize = +event.target.value;
    this.currentPage = 1;
    this.loadUsers(this.currentPage, this.pageSize);
  }

async deleteUser(userId: number) {
  try {
    const token = localStorage.getItem('authToken');
    if (!token) throw new Error('Auth token not found in localStorage');

    const response = await fetch(
      `${environment.apiUrl}/contact-us/${userId}`, // ✅ correct URL
      {
        method: 'DELETE',
        headers: {
          Authorization: `Bearer ${token}`,
          'Content-Type': 'application/json'
        }
      }
    );

    if (!response.ok) throw new Error('Failed to delete user');

    const result = await response.json();
    console.log('User deleted:', result);

    this.loadUsers(this.currentPage, this.pageSize);
  } catch (error) {
    console.error('Error deleting user:', error);
  }
}


}
